<template>
  <div>
    <blog-header></blog-header>
    <blog-body></blog-body>
    <div class="progressive parallax-window ">
      <img class="preview" v-progressive="bg.lowRes" :data-srcset="bg.original" :src="bg.original" />
    </div>
  </div>
</template>

<style lang="scss">
@import "../../../public/scss/mixins/_set-color.scss";
body {
  margin: 0;
  padding: 0;
  height: 100%;
  color: $color-font;
  font-size: 100%;
  font-size: 16px;
  font-size: 1em;
  width: 100%; // background-image: url(/images/dark_geometric2.png);
  background-color: $color-bg;

  overflow-x: hidden;
}
.p10 {
  padding: 10px;
}

.p10-lr {
  padding-left: 10px;
  padding-right: 10px;
}
.parallax-window {
  position: absolute;
  min-height: 100%;
  width: 100%;
  left: 0;
  top: 0;
  z-index: -1;
  transition: transform 0.001s ease;

  > img {
    width: 100%;
  }

  &:after {
    content: "";
    position: absolute;
    width: 100%;
    height: 50%;
    bottom: 0;
    background-image: linear-gradient(
      to bottom,
      rgba(231, 51, 104, 0),
      $color-bg
    );
  }
}
</style>

<script>
import Header from "../../components/blog/header";
import Body from "../../components/blog/body";
import "../../../public/css/icon/blog.css";
import "../../../node_modules/progressive-image/dist/index.css";
export default {
  name: "Blog",
  data() {
    return {
      bg: {
        original: "/images/blog_bg.jpg",
        lowRes: "/images/placeholder/blog_bg.jpg"
      }
    };
  },
  components: {
    "blog-header": Header,
    "blog-body": Body
  }
};
</script>


